.c-portada {
  width: 100%;
  height: 100vh;
  background-image: url(../img/portada.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: left;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-position 3s ease;
}
.c-portada:hover {
  background-position: bottom;
}
.c-portada:hover .c-portada__button {
  transform: scale(1.2);
}
.c-portada:hover .c-portada__h1 {
  transform: scale(1.2);
}
.c-portada:hover .c-portada__h2 {
  transform: scale(1.1);
}
.c-portada__container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.c-portada__h1 {
  color: #fff;
  font-weight: 800;
  font-size: 2.8em;
  font-style: italic;
  padding: 1rem;
  transition: transform 1.5s ease;
}
.c-portada__h2 {
  color: #fff;
  font-size: 1.25em;
  padding: 2rem 0;
  transition: transform 1.5s ease;
}
.c-portada__button {
  display: flex;
  justify-content: center;
  margin: 4rem 0 0;
  transition: transform 1.5s ease;
}