// @use 'settings/variables' as v;
// @use 'tools/mixins' as m;


.c-portada {
    width: 100%;
    height: 100vh;
    background-image: url(../img/portada.jpg);
    background-size: cover;
    background-attachment: fixed;
    background-position: left;
    // @include m.flex(row, nowrap, center, center);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-position 3s ease;
    &:hover {
        background-position: bottom;
        .c-portada__button {
            transform: scale(1.2);
        }
        .c-portada__h1 {
            transform: scale(1.2);
        }
        .c-portada__h2 {
            transform: scale(1.1);
        }
    }

    &__container {
        // @include m.flex(row, nowrap, center, center);
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
    &__h1 {
        color: #fff;
        font-weight: 800;
        font-size: 2.8em;
        font-style: italic;
        padding: 1rem;
        transition: transform 1.5s ease;
    }
    &__h2 {
        color: #fff;
        font-size: 1.25em;
        padding: 2rem 0;
        transition: transform 1.5s ease;
    }
    &__button {
        display: flex;
        justify-content: center;
        margin: 4rem 0 0;
        transition: transform 1.5s ease;
    }
}